import React, { FunctionComponent, useContext } from 'react';
import CSV from 'csvtojson';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField
} from '@mui/material';

import { GlobalContext } from 'utils/GlobalContext';

import { TextBold } from './styled';

const HEADERS = ['address', 'privateKey'];

const PrivateKeysModalTrigger: FunctionComponent = () => {
  const globalContext = useContext(GlobalContext);
  const [open, setOpen] = React.useState(false);
  const [value, setValue] = React.useState('');

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const storePrivateKeysLocally = async () => {
    const privateKeys = await CSV({
      headers: HEADERS,
      noheader: true,
      checkColumn: true
    }).fromString(value);

    const newPrivateKeys: { [key: string]: string } = {};

    privateKeys.forEach((privateKeysObject) => {
      const address = privateKeysObject.address.toLowerCase();
      const privateKey = privateKeysObject.privateKey.toLowerCase();
      newPrivateKeys[address] = privateKey;
    });

    globalContext.setPrivateKeys(newPrivateKeys);
    handleClose();
  };

  return (
    <div>
      <Button onClick={handleClickOpen}>
        <TextBold>Set private keys</TextBold>
      </Button>

      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Add private keys</DialogTitle>
        <DialogContent>
          <DialogContentText>
            CSV format:
            <br />
            {HEADERS.join(', ')}
            <br />
            <br />
          </DialogContentText>
          <TextField
            multiline
            minRows={10}
            placeholder="ronin:tyuio,0xabcd"
            style={{ width: '100%', minWidth: '500px' }}
            value={value}
            onChange={(event) => setValue(event.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={storePrivateKeysLocally}>Confirm</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default PrivateKeysModalTrigger;
