import { utils, UnsignedTransaction } from 'ethers';
import TrezorConnect, { EthereumTransaction } from 'trezor-connect';

import { EthereumTransactionValues, EthereumMessageValues } from 'types/types';
import { toCustomError } from 'utils/CustomError';

TrezorConnect.manifest({
  email: 'fabra.livenation@gmail.com',
  appUrl: 'https://metamask.io'
});

class TrezorSigner {
  static signMessage = async (
    messageValues: EthereumMessageValues
  ): Promise<string> => {
    try {
      const response = await TrezorConnect.ethereumSignMessage({
        path: messageValues.walletPath,
        message: messageValues.message
      });

      if (!response.success) {
        throw response.payload;
      }

      const signedMessage = response.payload.signature;
      return signedMessage;
    } catch (error) {
      throw toCustomError({
        slug: 'TrezorSigner.signMessage.error',
        error
      });
    }
  };

  static signTransaction = async (
    transactionValues: EthereumTransactionValues
  ): Promise<string> => {
    try {
      const transaction: EthereumTransaction = {
        chainId: transactionValues.chainId,
        data: transactionValues.data,
        gasLimit: transactionValues.gasLimit,
        gasPrice: transactionValues.gasPrice,
        nonce: transactionValues.nonceString,
        to: transactionValues.to,
        value: transactionValues.value
      };

      const response = await TrezorConnect.ethereumSignTransaction({
        path: transactionValues.walletPath,
        transaction
      });

      if (!response.success) {
        throw response.payload.error;
      }

      const signature = {
        v: parseInt(response.payload.v.substring(2), 16),
        r: response.payload.r,
        s: response.payload.s
      };

      const unsignedTransaction: UnsignedTransaction = {
        ...transaction,
        nonce: transactionValues.nonceNumber
      };

      const rawSignedTransaction = utils.serializeTransaction(
        unsignedTransaction,
        signature
      );

      return rawSignedTransaction;
    } catch (error) {
      throw toCustomError({
        slug: 'TrezorSigner.signTransaction.error',
        error
      });
    }
  };
}

export default TrezorSigner;
