import styled from 'styled-components';

export const Container = styled.div`
  align-items: center;
  background-color: #1e1e1e;
  border-radius: 7px;
  display: flex;
  justify-content: space-around;
  min-height: 90px;
  padding: 0 20px;
  width: 100%;
`;

export const Column = styled.div`
  padding: 0 20px;
  text-align: left;
`;

export const TextBold = styled.span`
  font-weight: 600;
`;
