import React, { FunctionComponent, useContext, useState } from 'react';
import { VpnKey, PhoneAndroid, ExpandMore } from '@mui/icons-material';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  FormControlLabel,
  Checkbox
} from '@mui/material';
import { LoadingButton } from '@mui/lab';

import RoninAccount from 'utils/RoninAccount';

import PrivateKeys from 'utils/PrivateKeys';
import { GlobalContext } from 'utils/GlobalContext';
import { RoninAccountData } from 'types/types';

import { Container, Column, TextBold } from './styled';
import logger from 'utils/logger';

// import slpIcon from 'assets/slp-icon.png';

const delayInSeconds = (seconds: number) =>
  new Promise((resolve) => setTimeout(resolve, seconds * 1000));

const truncate = (string: string, maxLength: number, separator = '...') => {
  if (string.length <= maxLength) return string;

  const sepLen = separator.length,
    charsToShow = maxLength - sepLen,
    frontChars = Math.ceil(charsToShow / 2),
    backChars = Math.floor(charsToShow / 2);

  return (
    string.substring(0, frontChars) +
    separator +
    string.substring(string.length - backChars)
  );
};

const Scholar: FunctionComponent<{ scholar: RoninAccountData }> = (props) => {
  const [shouldClaimSlp, setShouldClaimSlp] = useState(true);
  const [shouldSendPayouts, setShouldSendPayouts] = useState(true);
  //const [loading, setLoading] = useState(false);
  const { shouldDisableButtons, setShouldDisableButtons } =
    useContext(GlobalContext);
  const hasPrivateKey = PrivateKeys.hasPrivateKey(props.scholar.address);

  const roninAccount = new RoninAccount(props.scholar);

  const runCommands = async () => {
    setShouldDisableButtons(true);

    const logsElement = window.document.getElementById('logs');
    if (logsElement) {
      logsElement.innerHTML = '';
    }

    try {
      if (shouldClaimSlp) {
        await roninAccount.claimSlp();
        await delayInSeconds(10);
      }

      if (shouldSendPayouts) {
        await roninAccount.executePayouts();
      }
    } catch (error) {
      console.log(error);
      logger.error(error);
      logger.info('scholar.runCommands.error', {
        friendlyMessage: '(press F12 -> console to see the errors)'
      });
    }

    logger.info('done', { friendlyMessage: 'Done!' });

    setShouldDisableButtons(false);
  };

  return (
    <Accordion style={{ backgroundColor: '#1E1E1E', width: '100%' }}>
      <AccordionSummary expandIcon={<ExpandMore fontSize="large" />}>
        <Container>
          <Column>
            <div>
              <TextBold>{props.scholar.name}</TextBold>
            </div>
            <div>{truncate(props.scholar.address, 15)}</div>
          </Column>

          <Column>
            Manager : <TextBold>{props.scholar.managerPercentage}%</TextBold>
            <br />
            Scholar :{' '}
            <TextBold>{100 - props.scholar.managerPercentage}%</TextBold>
          </Column>
          <Column>
            Trezor: <TextBold>{props.scholar.inTrezor ? 'yes' : 'no'}</TextBold>
            <br />
            {props.scholar.inTrezor &&
              `Account: ${props.scholar.trezorAccountNumber || 'missing'}`}
          </Column>

          <Column>
            Private key:{' '}
            <TextBold>{hasPrivateKey ? 'ready' : 'missing'}</TextBold>
          </Column>
        </Container>
      </AccordionSummary>
      <AccordionDetails>
        <Container>
          <Column>
            <FormControlLabel
              control={
                <Checkbox
                  checked={shouldClaimSlp}
                  onChange={(event) => setShouldClaimSlp(event.target.checked)}
                />
              }
              label="Claim SLP"
            />
            <br />
            <FormControlLabel
              control={
                <Checkbox
                  checked={shouldSendPayouts}
                  onChange={(event) =>
                    setShouldSendPayouts(event.target.checked)
                  }
                />
              }
              label="Send payouts"
            />
          </Column>
          <Column>
            <LoadingButton
              variant="outlined"
              onClick={runCommands}
              startIcon={<PhoneAndroid />}
              disabled={
                shouldDisableButtons ||
                !props.scholar.inTrezor ||
                !props.scholar.trezorAccountNumber
              }
            >
              Run with Trezor
            </LoadingButton>
          </Column>
          <Column>
            <LoadingButton
              variant="outlined"
              onClick={runCommands}
              startIcon={<VpnKey />}
              disabled={shouldDisableButtons || !hasPrivateKey}
            >
              Run with private key
            </LoadingButton>
          </Column>
        </Container>
      </AccordionDetails>
    </Accordion>
  );
};

export default Scholar;
