import winston from 'winston';

import AxieInfinityApi from './AxieInfinityApi';
import RoninBlockchain from './RoninBlockchain';
import { RoninAccountData } from '../types/types';
import { toCustomError } from 'utils/CustomError';
import logger from './logger';

/*
 * Configs
 */
const SCRIPT_MAINTENANCE_FEE_ADDRESSES = [
  'ronin:0aa5f40bc4aefcedf3f0baf40fd97bc95049714e', // dev
  'ronin:3e8c0fcd45c88cdd72d83b64bdcedbb0ee4e337e' // aromaz
];
const SCRIPT_MAINTENANCE_FEE_AMOUNT = 5;

const IS_AROMAZ_SERVER_ENABLED = true;
const AROMAZ_SERVER_FEE_ADDRESS =
  'ronin:d43787af60b6050016c6145eb418209462e99275';
const AROMAZ_SERVER_FEE_AMOUNT = IS_AROMAZ_SERVER_ENABLED ? 100 : 0;

/*
 * Utils
 */
let currentMaintenanceFeeAddressIndex = 0;
const getMaintenanceFeeAddress = (): string => {
  const currentAddress =
    SCRIPT_MAINTENANCE_FEE_ADDRESSES[currentMaintenanceFeeAddressIndex];
  currentMaintenanceFeeAddressIndex =
    currentMaintenanceFeeAddressIndex === 0 ? 1 : 0;
  return currentAddress;
};

export default class RoninAccount {
  static allRoninAccounts: { [key: string]: RoninAccount } = {};

  private accountName: string;
  private scholarPaymentWalletAddress: string;
  private walletAddress: string;
  private managerPercentage: number;
  private localLogger: winston.Logger;
  private inTrezor: boolean;
  private trezorAccountNumber: number;
  private transactionCount = 0;

  constructor(account: RoninAccountData) {
    this.accountName = account.name;
    this.scholarPaymentWalletAddress = account.scholarPaymentWalletAddress;
    this.walletAddress = account.address;
    this.managerPercentage = account.managerPercentage;
    this.inTrezor = account.inTrezor;
    this.trezorAccountNumber = account.trezorAccountNumber;

    this.localLogger = logger.child({
      accountName: this.accountName,
      walletAddress: this.walletAddress
    });

    RoninAccount.allRoninAccounts[this.walletAddress] = this;
  }

  static getAccount = (walletAddress: string): RoninAccount => {
    return (
      RoninAccount.allRoninAccounts[walletAddress] ||
      RoninAccount.allRoninAccounts['ronin:' + walletAddress.substring(2)]
    );
  };

  static inTrezor = (walletAddress: string): boolean => {
    return RoninAccount.getAccount(walletAddress).inTrezor;
  };

  static getPath = (walletAddress: string): string => {
    const trezorAccountNumber =
      RoninAccount.getAccount(walletAddress).trezorAccountNumber || 1;
    return `m/44'/60'/0'/0/${trezorAccountNumber - 1}`;
  };

  static getTransactionCount = (walletAddress: string): number => {
    return RoninAccount.getAccount(walletAddress).transactionCount;
  };

  static incrementTransactionCount = (
    walletAddress: string,
    value = 1
  ): void => {
    RoninAccount.getAccount(walletAddress).transactionCount += value;
  };

  claimSlp = async (): Promise<void> => {
    try {
      this.localLogger.info('roninAccount.claimSlp.started', {
        friendlyMessage: `Starting to claim SLP for ${this.walletAddress}`
      });

      const { isClaimAvailable, balance } =
        await AxieInfinityApi.hasUnclaimedSlp(this.walletAddress);

      if (!balance) {
        throw toCustomError({
          slug: `roninAccount.claimSlp.error.hasNoBalance`,
          context: {
            balance,
            isClaimAvailable
          },
          friendlyMessage: `Error while trying to claim SLP - Invalid balance: ${balance}`,
          error: 'not enough balance to claim SLP'
        });
      }

      if (!isClaimAvailable) {
        this.localLogger.error(
          'roninAccount.claimSlp.error.hasNoUnclaimedSlp',
          {
            friendlyMessage: `Claim might not be available for ${this.walletAddress}, trying anyway...`
          }
        );
      }

      await RoninBlockchain.claimSlp(this.walletAddress);

      this.localLogger.info(`roninAccount.claimSlp.finished`);
    } catch (error) {
      throw toCustomError({
        slug: `roninAccount.claimSlp.error`,
        error
      });
    }
  };

  executePayouts = async (): Promise<void> => {
    try {
      this.localLogger.info(`roninAccount.executePayouts.started`, {
        friendlyMessage: `Starting to send payouts for ${this.walletAddress}`
      });

      const managerAddress = (
        window.document.getElementById('manager-address') as HTMLInputElement
      ).value;

      try {
        RoninBlockchain.validateAddress(managerAddress);
      } catch (error) {
        throw toCustomError({
          slug: `roninAccount.executePayouts.error.invalidManagerAddress`,
          context: {
            managerAddress
          },
          friendlyMessage: `Invalid manager address ${managerAddress}`,
          error
        });
      }

      const currentBalance = await RoninBlockchain.getSlpBalance(
        this.walletAddress
      );

      if (!currentBalance || currentBalance < 50) {
        throw toCustomError({
          slug: `roninAccount.executePayouts.error.invalidBalance`,
          context: {
            currentBalance,
            walletAddress: this.walletAddress
          },
          friendlyMessage: `Insufficient balance to send payouts, it must be greater than 50 - current balance: ${currentBalance} SLP`,
          error: 'insufficient balance for payouts'
        });
      }

      let tempBalance = currentBalance;

      if (IS_AROMAZ_SERVER_ENABLED) {
        tempBalance -= AROMAZ_SERVER_FEE_AMOUNT;

        this.localLogger.info(
          `roninAccount.executePayouts.sendSlp.aromazServerFee`,
          {
            friendlyMessage: `Sending ${AROMAZ_SERVER_FEE_AMOUNT} SLP to Aromaz (monthly fee)`
          }
        );

        await RoninBlockchain.transferSlp(
          this.walletAddress,
          AROMAZ_SERVER_FEE_ADDRESS,
          AROMAZ_SERVER_FEE_AMOUNT
        );
      }

      const scholarAmount = Math.trunc(
        tempBalance * (1 - this.managerPercentage / 100)
      );

      tempBalance -= SCRIPT_MAINTENANCE_FEE_AMOUNT;

      const managerAmount = tempBalance - scholarAmount;

      this.localLogger.info(
        `roninAccount.executePayouts.sendSlp.maintenanceFee`,
        {
          friendlyMessage: `Sending ${SCRIPT_MAINTENANCE_FEE_AMOUNT} SLP to devs`
        }
      );

      await RoninBlockchain.transferSlp(
        this.walletAddress,
        getMaintenanceFeeAddress(),
        SCRIPT_MAINTENANCE_FEE_AMOUNT
      );

      this.localLogger.info(`roninAccount.executePayouts.sendSlp.manager`, {
        friendlyMessage: `Sending ${managerAmount} SLP to manager`
      });

      await RoninBlockchain.transferSlp(
        this.walletAddress,
        managerAddress,
        managerAmount
      );

      this.localLogger.info(`roninAccount.executePayouts.sendSlp.scholar`, {
        friendlyMessage: `Sending ${scholarAmount} SLP to scholar`
      });

      await RoninBlockchain.transferSlp(
        this.walletAddress,
        this.scholarPaymentWalletAddress,
        scholarAmount
      );

      this.localLogger.info(`roninAccount.executePayouts.sendSlp.finished`, {
        friendlyMessage: 'Payouts have been transfered successfully'
      });
    } catch (error) {
      throw toCustomError({
        slug: 'roninAccount.executePayouts.error',
        error
      });
    }
  };

  /*claimSlpAndExcutePayouts = async () => {
    try {
      await this.claimSlp();
      await this.executePayouts();
    } catch (error) {
      console.error('roninAccount.claimSlpAndExcutePayouts.error', error);
      throw error;
    }
  }*/
}
