export default class PrivateKeys {
  private static _privateKeys: { [key: string]: string } = {};
  static amountOfUpdates = 0;

  static findPrivateKey = (roninWalletAddress: string): string => {
    return (
      PrivateKeys._privateKeys[roninWalletAddress] ||
      PrivateKeys._privateKeys['ronin:' + roninWalletAddress.substring(2)]
    );
  };

  static hasPrivateKey = (roninWalletAddress: string): boolean => {
    return Boolean(PrivateKeys.findPrivateKey(roninWalletAddress));
  };

  static setPrivateKeys = (
    privateKeys: typeof PrivateKeys._privateKeys
  ): void => {
    PrivateKeys._privateKeys = privateKeys;
    PrivateKeys.amountOfUpdates += 1;
  };
}
