import React, { FunctionComponent, useContext } from 'react';
//import ReactMarkdown from 'react-markdown';
import { CircularProgress } from '@mui/material';

import { GlobalContext } from 'utils/GlobalContext';

import { Container } from './styled';

const LogsMonitor: FunctionComponent = () => {
  const logsElement = window.document.getElementById('logs');
  const { shouldDisableButtons } = useContext(GlobalContext);
  const isVisible = shouldDisableButtons || logsElement?.innerHTML;

  return (
    <div style={{ display: isVisible ? 'block' : 'none' }}>
      Current process
      <br />
      <br />
      {shouldDisableButtons && (
        <>
          <CircularProgress size={20} />
          <br />
          <br />
        </>
      )}
      <Container id="logs" />
    </div>
  );
};

export default LogsMonitor;
