import React, { FunctionComponent } from 'react';
import CSV from 'csvtojson';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField
} from '@mui/material';

import { RoninAccountData } from 'types/types';

import { Container, Column, TextBold } from '../Scholar/styled';

const HEADERS = [
  'address',
  'name',
  'managerPercentage',
  'scholarPaymentAddress',
  'inTrezor',
  'trezorAccountNumber'
];

const AddScholarsTrigger: FunctionComponent<{
  addMoreScholars: (newScholars: RoninAccountData[]) => void;
}> = (props) => {
  const [open, setOpen] = React.useState(false);
  const [value, setValue] = React.useState('');

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const saveScholars = async () => {
    const scholars = await CSV({
      headers: HEADERS,
      noheader: true,
      checkColumn: true
    }).fromString(value);
    const formattedScholars: RoninAccountData[] = scholars.map((scholar) => {
      return {
        address: scholar.address.toLowerCase(),
        inTrezor: scholar.inTrezor === 'false' ? false : true,
        managerPercentage: Number(scholar.managerPercentage),
        name: scholar.name,
        scholarPaymentWalletAddress:
          scholar.scholarPaymentAddress.toLowerCase(),
        trezorAccountNumber: Number(scholar.trezorAccountNumber) || 1
      };
    });

    props.addMoreScholars(formattedScholars);
    handleClose();
  };

  return (
    <Container /*style={{ backgroundColor: '#1E1E1E' }}*/>
      <Column>
        <Button onClick={handleClickOpen}>
          <TextBold>+ Add scholars</TextBold>
        </Button>
      </Column>

      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Add Scholars</DialogTitle>
        <DialogContent>
          <DialogContentText>
            CSV format:
            <br />
            {HEADERS.join(', ')}
            <br />
            <br />
          </DialogContentText>
          <TextField
            multiline
            minRows={10}
            placeholder="ronin:tyuio, John, 50, ronin:tyuio, true, 5"
            style={{ width: '100%', minWidth: '500px' }}
            value={value}
            onChange={(event) => setValue(event.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={saveScholars}>Add scholars</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default AddScholarsTrigger;
