import axios from 'axios';
import { addDays, isAfter } from 'date-fns';

import RoninBlockchain, { SignatureData } from './RoninBlockchain';
import { toCustomError } from 'utils/CustomError';
import logger from './logger';

export default class AxieInfinityApi {
  private static _generateAccessToken = async (walletAddress: string) => {
    try {
      const generateRoninMessageResponse = await axios(
        'https://graphql-gateway.axieinfinity.com/graphql',
        {
          method: 'POST',
          data: {
            operationName: 'CreateRandomMessage',
            variables: {},
            query: 'mutation CreateRandomMessage{createRandomMessage}'
          }
        }
      );
      const roninMessage =
        // @ts-expect-error TODO: create type for response
        generateRoninMessageResponse.data.data.createRandomMessage;

      logger.info('RoninBlockchain.claimSlp.finished', {
        walletAddress,
        friendlyMessage: `Signing message to log in to axie infinity marketplace account.`
      });

      const signature = await RoninBlockchain.signMessage(
        walletAddress,
        roninMessage
      );

      const ethAddress = RoninBlockchain.transformToEthWallet(walletAddress);

      const createAccessTokenResponse = await axios(
        'https://graphql-gateway.axieinfinity.com/graphql',
        {
          method: 'POST',
          data: {
            operationName: 'CreateAccessTokenWithSignature',
            variables: {
              input: {
                mainnet: 'ronin',
                owner: ethAddress,
                message: roninMessage,
                signature
              }
            },
            query:
              'mutation CreateAccessTokenWithSignature($input: SignatureInput!) {createAccessTokenWithSignature(input: $input) {newAccount result accessToken __typename}}'
          }
        }
      );

      const accessToken =
        // @ts-expect-error TODO: create type for response
        createAccessTokenResponse.data.data.createAccessTokenWithSignature
          .accessToken;
      return accessToken;
    } catch (error) {
      throw toCustomError({
        slug: 'AxieInfinityApi.generateAccessToken.error',
        friendlyMessage: `Error while generating marketplace credentials for ${walletAddress}`,
        error
      });
    }
  };

  static hasUnclaimedSlp = async (
    walletAddress: string
  ): Promise<{ isClaimAvailable: boolean; balance: number }> => {
    try {
      logger.info('AxieInfinityApi.hasUnclaimedSlp.started', {
        friendlyMessage: `Validating claim for ${walletAddress}`
      });

      const ethAddress = RoninBlockchain.transformToEthWallet(walletAddress);
      const url = `https://game-api.skymavis.com/game-api/clients/${ethAddress}/items/1`;
      const response = await axios(url, {
        method: 'GET'
      });

      // @ts-expect-error TODO: create type for response
      const balance = response.data.total;

      if (!balance || balance <= 0) {
        return { isClaimAvailable: false, balance };
      }

      // @ts-expect-error TODO: create type for response
      const lastClaimAt = response.data.last_claimed_item_at;
      const lastClaimAtDate = new Date(lastClaimAt * 1000);
      const nextClaimAt = addDays(lastClaimAtDate, 14);
      const now = new Date();
      const isClaimAvailable = isAfter(now, nextClaimAt);

      if (!isClaimAvailable) {
        return { isClaimAvailable: false, balance };
      }

      logger.info('AxieInfinityApi.hasUnclaimedSlp.finished', {
        friendlyMessage: `Claim is available for ${walletAddress}`
      });

      return { isClaimAvailable: true, balance };
    } catch (error) {
      throw toCustomError({
        slug: 'AxieInfinityApi.hasUnclaimedSlp.error',
        friendlyMessage: `Error while calculating unclaimed SLP for ${walletAddress}`,
        error
      });
    }
  };

  static getClaimSlpBlockchainSignature = async (
    walletAddress: string
  ): Promise<SignatureData> => {
    try {
      const authToken = await AxieInfinityApi._generateAccessToken(
        walletAddress
      );

      logger.info('AxieInfinityApi.getClaimSlpBlockchainSignature.started', {
        friendlyMessage: `Fetching blockchain claim transaction from the marketplace for ${walletAddress}`
      });

      const ethAddress = RoninBlockchain.transformToEthWallet(walletAddress);
      const response = await axios(
        `https://game-api.skymavis.com/game-api/clients/${ethAddress}/items/1/claim`,
        {
          method: 'POST',
          headers: {
            authorization: `Bearer ${authToken}`
          },
          data: {}
        }
      );

      const signatureData: SignatureData =
        // @ts-expect-error TODO: create type for response
        response.data.blockchain_related.signature;

      return signatureData;
    } catch (error) {
      throw toCustomError({
        slug: 'AxieInfinityApi.getClaimSlpBlockchainSignature.error',
        friendlyMessage: `Error while fetching transaction data from the marketplace for ${walletAddress}`,
        error
      });
    }
  };
}
