import { createLogger, format, transports } from 'winston';

/*
 * Formats
 */
const parseErrorsToString = format((data) => {
  if (data?.metadata?.error instanceof Error) {
    data.metadata.error = data.metadata.error.message;
  }
  return data;
});

const logInScreen = format((data) => {
  if (data?.friendlyMessage) {
    const logsElement = window.document.getElementById('logs');
    if (logsElement) {
      const message = `${data.level}: ${data.friendlyMessage}`;

      if (logsElement.innerHTML) {
        logsElement.innerHTML += '<br /><br />';
      }

      logsElement.innerHTML += message;

      logsElement.scrollTop = logsElement.scrollHeight;
    }

    delete data.friendlyMessage;
  }

  return data;
});

const logger = createLogger({
  level: 'info',
  exitOnError: false, // ?
  transports: [
    new transports.Console({
      format: format.combine(
        logInScreen(),
        format.errors({ stack: false }),
        format.metadata(),
        format.timestamp(),
        parseErrorsToString(),
        format.colorize(),
        format.simple()
      )
    })
  ]
});

export default logger;
