import { providers, Wallet } from 'ethers';

import PrivateKeys from 'utils/PrivateKeys';
import { EthereumTransactionValues, EthereumMessageValues } from 'types/types';
import { toCustomError } from 'utils/CustomError';

class PrivateKeySigner {
  static signMessage = async (
    messageValues: EthereumMessageValues
  ): Promise<string> => {
    try {
      const wallet = new Wallet(PrivateKeys.findPrivateKey(messageValues.from));

      if (wallet.address.toLowerCase() !== messageValues.from) {
        throw toCustomError({
          slug: 'PrivateKeySigner.signMessage.error.invalidPrivateKey',
          friendlyMessage: 'Given private key for this address is not correct',
          error: 'invalid private key'
        });
      }

      const signedMessage = await wallet.signMessage(messageValues.message);

      return signedMessage;
    } catch (error) {
      throw toCustomError({
        slug: 'PrivateKeySigner.signMessage.error',
        error
      });
    }
  };

  static signTransaction = async (
    transactionValues: EthereumTransactionValues
  ): Promise<string> => {
    try {
      const transaction: providers.TransactionRequest = {
        chainId: transactionValues.chainId,
        data: transactionValues.data,
        from: transactionValues.from,
        gasLimit: transactionValues.gasLimit,
        gasPrice: transactionValues.gasPrice,
        nonce: transactionValues.nonceNumber,
        to: transactionValues.to
      };

      const wallet = new Wallet(
        PrivateKeys.findPrivateKey(transactionValues.from)
      );

      if (wallet.address.toLowerCase() !== transaction.from) {
        throw toCustomError({
          slug: 'PrivateKeySigner.signTransaction.error.invalidPrivateKey',
          friendlyMessage: 'Given private key for this address is not correct',
          error: 'invalid private key'
        });
      }

      const signedTransaction = await wallet.signTransaction(transaction);

      return signedTransaction;
    } catch (error) {
      throw toCustomError({
        slug: 'PrivateKeySigner.signTransaction.error',
        error
      });
    }
  };
}

export default PrivateKeySigner;
