import React, { FunctionComponent, useState } from 'react';
import { TextField, List, ListItem } from '@mui/material';

import { Container, Logo, Main, SideBar, ListHeader } from './styled';

import Scholar from './Scholar/Scholar';
import AddScholarsTrigger from './AddScholarsTrigger/AddScholarsTrigger';
import PrivateKeysModalTrigger from './PrivateKeysModalTrigger/PrivateKeysModalTrigger';
import LogsMonitor from 'components/LogsMonitor/LogsMonitor';
import { RoninAccountData } from 'types/types';

import logo from 'assets/logo2.png';

const DEFAULT_SCHOLARS: RoninAccountData[] = [];

const HomeScreen: FunctionComponent = () => {
  const [scholars, setScholars] =
    useState<RoninAccountData[]>(DEFAULT_SCHOLARS);

  const addMoreScholars = (newScholars: RoninAccountData[]) => {
    setScholars(newScholars);
  };

  const renderScholars = () => {
    return scholars.map((scholar) => {
      return (
        <ListItem key={scholar.address}>
          <Scholar scholar={scholar} />
        </ListItem>
      );
    });
  };

  return (
    <Container>
      <Main>
        <LogsMonitor />
        <h2>Manager</h2>
        <TextField
          label="Manager Address (Ronin)"
          variant="outlined"
          style={{ width: '500px' }}
          id="manager-address"
        />
        <br />
        <br />
        <h2>Scholars</h2>
        <ListHeader>
          <PrivateKeysModalTrigger />
        </ListHeader>

        <List>
          {renderScholars()}
          {
            <ListItem>
              <AddScholarsTrigger addMoreScholars={addMoreScholars} />
            </ListItem>
          }
        </List>
      </Main>
    </Container>
  );
};

export default HomeScreen;

