import Web3 from 'web3';
import { AbiItem } from 'web3-utils/types';
import { utils } from 'ethers';

import logger from './logger';
import RoninAccount from 'utils/RoninAccount';
import { EthereumTransactionValues, EthereumMessageValues } from 'types/types';
import TrezorSigner from 'utils/TrezorSigner';
import { toCustomError } from 'utils/CustomError';
import PrivateKeySigner from 'utils/PrivateKeySigner';
import AxieInfinityApi from './AxieInfinityApi';
import SLP_ABI from 'assets/slp_abi.json';

const SLP_CONTRACT = '0xa8754b9fa15fc18bb59458815510e40a12cd2014';
const SLP_CONTRACT_RONIN = 'ronin:a8754b9fa15fc18bb59458815510e40a12cd2014';
const RONIN_PROVIDER_FREE = 'https://proxy.roninchain.com/free-gas-rpc';
const RONIN_PROVIDER = 'https://api.roninchain.com/rpc';
const CHAIN_ID = 2020;
const GAS_LIMIT = utils.hexlify(100000);
const GAS_PRICE = utils.hexlify(0);
const FREE_VALUE = utils.hexlify(0);

const web3RoninProviderFree = new Web3(RONIN_PROVIDER_FREE);
const slpContract = new web3RoninProviderFree.eth.Contract(
  SLP_ABI as AbiItem[],
  SLP_CONTRACT
);

const web3RoninProvider = new Web3(RONIN_PROVIDER);
const slpContract2 = new web3RoninProvider.eth.Contract(
  SLP_ABI as AbiItem[],
  SLP_CONTRACT
);

export interface SignatureData {
  amount: number;
  signature: string;
  timestamp: number;
}

export default class RoninBlockchain {
  static validateAddress = (roninWalletAddres: string): boolean => {
    let isValid = false;

    try {
      isValid = Boolean(
        roninWalletAddres.startsWith('ronin:') &&
          utils.getAddress(roninWalletAddres.replace('ronin:', '0x'))
      );
    } catch (error) {
      isValid = false;
    }

    if (!isValid) {
      throw toCustomError({
        slug: 'RoninBlockchain.validateAddress.error',
        friendlyMessage: `Invalid ronin address: ${roninWalletAddres}`,
        context: {
          roninWalletAddres
        },
        error: 'invalid ronin address'
      });
    }

    return isValid;
  };

  static transformToEthWallet = (roninWalletAddres: string): string => {
    RoninBlockchain.validateAddress(roninWalletAddres);
    return roninWalletAddres.replace('ronin:', '0x').toLocaleLowerCase();
  };

  private static _getTransactionCount = async (roninWalletAddres: string) => {
    const finalWalletAddres = this.transformToEthWallet(roninWalletAddres);

    const localCount = RoninAccount.getTransactionCount(roninWalletAddres);
    const blockchainCount = await web3RoninProviderFree.eth.getTransactionCount(
      finalWalletAddres
    );

    const finalCount = Math.max(localCount, blockchainCount);

    RoninAccount.incrementTransactionCount(
      roninWalletAddres,
      finalCount - localCount
    );

    return finalCount;
  };

  private static _signAndSendTransaction = async (
    from: string,
    to: string,
    transactionData: string
  ) => {
    try {
      // Calculate all the data needed for the transactions
      const nonce = await this._getTransactionCount(from);
      const transactionValues: EthereumTransactionValues = {
        chainId: CHAIN_ID,
        data: transactionData,
        from: this.transformToEthWallet(from),
        gasLimit: GAS_LIMIT,
        gas: GAS_LIMIT,
        gasPrice: GAS_PRICE,
        nonceNumber: nonce,
        nonceString: utils.hexlify(nonce),
        to: this.transformToEthWallet(to),
        value: FREE_VALUE,
        walletPath: RoninAccount.getPath(from)
      };

      let rawSignedTransaction: string;

      if (RoninAccount.inTrezor(from)) {
        rawSignedTransaction = await TrezorSigner.signTransaction(
          transactionValues
        );
      } else {
        rawSignedTransaction = await PrivateKeySigner.signTransaction(
          transactionValues
        );
      }

      const receipt = await web3RoninProviderFree.eth.sendSignedTransaction(
        rawSignedTransaction
      );

      RoninAccount.incrementTransactionCount(from);

      return receipt.transactionHash;
    } catch (error) {
      throw toCustomError({
        slug: 'RoninBlockchain._signAndSendTransaction.error',
        error
      });
    }
  };

  static signMessage = async (
    roninWalletAddres: string,
    message: string
  ): Promise<string> => {
    const messageValues: EthereumMessageValues = {
      from: this.transformToEthWallet(roninWalletAddres),
      message,
      walletPath: RoninAccount.getPath(roninWalletAddres)
    };
    let signedMessage: string;

    if (RoninAccount.inTrezor(roninWalletAddres)) {
      signedMessage = await TrezorSigner.signMessage(messageValues);
    } else {
      signedMessage = await PrivateKeySigner.signMessage(messageValues);
    }

    return signedMessage;
  };

  static getSlpBalance = async (roninWalletAddres: string): Promise<number> => {
    const finalWalletAddres = this.transformToEthWallet(roninWalletAddres);
    const transaction = slpContract2.methods.balanceOf(finalWalletAddres);
    const balance = await transaction.call();
    return Number(balance);
  };

  static transferSlp = async (
    from: string,
    to: string,
    amount: number
  ): Promise<{
    transactionHash: string;
    from: string;
    to: string;
    amount: number;
  }> => {
    try {
      if (amount <= 0 || !Number.isInteger(amount)) {
        throw new Error('RoninBlockchain.transferSlp.error.invalidAmount');
      }

      logger.info('RoninBlockchain.transferSlp.started', {
        from,
        to,
        amount,
        friendlyMessage: `Sending ${amount} SLP from ${from} to ${to}`
      });

      const ethTo = this.transformToEthWallet(to);

      const transactionData = slpContract.methods
        .transfer(ethTo, amount)
        .encodeABI();

      const transactionHash = await RoninBlockchain._signAndSendTransaction(
        from,
        SLP_CONTRACT_RONIN,
        transactionData
      );
      logger.info('RoninBlockchain.transferSlp.finished', {
        from,
        to,
        amount,
        transactionHash,
        friendlyMessage: `Transfer SLP transaction Completed - TX: ${transactionHash} - ${amount} SLP from ${from} to ${to}`
      });
      return { transactionHash, from, to, amount };
    } catch (error) {
      throw toCustomError({
        slug: 'RoninBlockchain.transferSlp.error',
        friendlyMessage: `Something when wrong while sending ${amount} SLP from ${from} to ${to}`,
        context: {
          from,
          to,
          amount
        },
        error
      });
    }
  };

  static claimSlp = async (roninWalletAddres: string): Promise<string> => {
    try {
      const signatureData =
        await AxieInfinityApi.getClaimSlpBlockchainSignature(roninWalletAddres);

      const transactionData = slpContract.methods
        .checkpoint(
          this.transformToEthWallet(roninWalletAddres),
          signatureData.amount,
          signatureData.timestamp,
          signatureData.signature
        )
        .encodeABI();

      logger.info('RoninBlockchain.claimSlp.started', {
        roninWalletAddres,
        friendlyMessage: `Initating transaction to claim SLPs for ${roninWalletAddres}`
      });

      const transactionHash = await RoninBlockchain._signAndSendTransaction(
        roninWalletAddres,
        SLP_CONTRACT_RONIN,
        transactionData
      );
      logger.info('RoninBlockchain.claimSlp.finished', {
        roninWalletAddres,
        transactionHash,
        friendlyMessage: `SLPs have been claimed for ${roninWalletAddres} - TX: ${transactionHash}`
      });
      return transactionHash;
    } catch (error) {
      throw toCustomError({
        slug: 'RoninBlockchain.claimSlp.error',
        context: {
          roninWalletAddres
        },
        error
      });
    }
  };
}
