export function isCustomError(item: any | undefined): item is CustomError {
  if (!item || !(item instanceof CustomError)) return false;

  return item.slugs?.length !== undefined;
}

/**
 * Creates a CameoError
 * @param slug An error slug to be logged in our logging provider.
 * @param message An internal error message.
 * @param userMessage A helpful user-facing message to be returned to the client.
 */
export class CustomError extends Error {
  /**
   * A user-facing error message
   */
  friendlyMessage?: string | undefined;
  friendlyMessages?: string[] = [];

  /**
   * A stack of slugs, used to simulate a stack-trace
   */
  slugs: string[] = [];

  /**
   * Context about the error, to be logged.
   */
  context: { [key: string]: any } | undefined;

  constructor({
    slug,
    message,
    friendlyMessage,
    context
  }: {
    slug?: string | undefined;
    message?: string | undefined;
    friendlyMessage?: string | undefined;
    context?: { [key: string]: any } | undefined;
  }) {
    super(slug ?? message ?? friendlyMessage ?? 'Something went wrong');
    if (slug) {
      this.slugs.push(slug);
    }
    this.friendlyMessage = friendlyMessage;
    this.context = context || {};
  }
}

/**
 * Transforms an item into a CameoError and merges contexts
 * @param error the item to transform
 * @param slug An error slug to be logged in our logging provider (e.g. 'cameoapi.controllers.someController.someError')
 * @param message A Cameo-internal error message.
 * @param context An object, to be logged in our logging provider
 * @param friendlyMessage A helpful user-facing error message to be returned to the client.
 */
export const toCustomError = ({
  error,
  slug,
  message,
  context,
  friendlyMessage
}: {
  error: any;
  slug: string | undefined;
  message?: string | undefined;
  context?: { [key: string]: any };
  friendlyMessage?: string | undefined;
}): CustomError => {
  // If we already have a CameoError, add context and passthrough
  if (isCustomError(error)) {
    if (slug) {
      error.slugs.push(slug);
    }

    // Add the user-facing error message if one doesn't already exist
    if (!error.friendlyMessage && friendlyMessage) {
      error.friendlyMessage = friendlyMessage;
    }

    if (error.friendlyMessage && friendlyMessage) {
      error.friendlyMessages?.push(friendlyMessage);
    }

    // Merge the contexts, favoring the topmost values of duplicates
    if (context) {
      error.context = Object.assign(error.context, context);
    }

    return error;
  }

  // If we have a string, wrap in a CameoError, using the string as the internal message
  if (typeof error === 'string') {
    return new CustomError({
      slug,
      message: error,
      context,
      friendlyMessage
    });
  }

  // We have some other type of object, wrap it in a CameoError
  const cameoError = new CustomError({
    slug,
    message: error.message ?? message,
    context,
    friendlyMessage
  });

  const wrappedError = Object.assign(cameoError, error);

  // This won't be assigned across automatically above, because the
  // `Error.stack` property is marked as non-enumerable.
  if (error.stack) {
    wrappedError.stack = error.stack;
  }

  return wrappedError;
};
