import React, { FunctionComponent, useState } from 'react';
import PrivateKeys from 'utils/PrivateKeys';

export const DEFAULT_GLOBAL_CONTEXT = {
  amountOfPrivateKeysUpdates: 0,
  shouldDisableButtons: false,
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  setPrivateKeys: (param1: any) => {},
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  setShouldDisableButtons: (param1: boolean) => {}
};

export const GlobalContext = React.createContext(DEFAULT_GLOBAL_CONTEXT);

export const GlobalProvider: FunctionComponent = (props) => {
  const [amountOfPrivateKeysUpdates, setAmountOfPrivateKeysUpdates] =
    useState(0);
  const [shouldDisableButtons, setShouldDisableButtons] = useState(false);

  const setPrivateKeys = (newPrivateKeys: any) => {
    // We need to access these keys from non-react classes / utils
    PrivateKeys.setPrivateKeys(newPrivateKeys);

    setAmountOfPrivateKeysUpdates(amountOfPrivateKeysUpdates + 1);
  };

  // Context values passed to consumer
  const value = {
    shouldDisableButtons,
    setShouldDisableButtons,
    amountOfPrivateKeysUpdates,
    setPrivateKeys
  };

  return (
    <GlobalContext.Provider value={value}>
      {props.children}
    </GlobalContext.Provider>
  );
};
